@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.css);
body {
  margin: 0;
  font-family: 'Papyrus', fantasy, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'ByHand';
  src: local('ByHand'), url(/static/media/byHand.b494a0e1.ttf) format('truetype');
}

.about{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
}

.presentation-header{
    font-size: 3rem;
}

.pitch{
    font-style: italic;
    margin: 2rem;
}

.handpan-img {
    width: 50%;
    height: auto;
    background-image: url(/static/media/handpan.35963f53.jpeg);
    background-size: cover;
}

.presentation{
    
}

@media screen and (max-width: 768px) {
    .presentation-header{
        font-size: 1rem;
        font-weight: bold;
    }

    .pitch{
        margin: 0rem;
        margin-bottom: 0.8rem;
        margin-top: 0.8rem;
    }
    
    .presentation{
        
    }

    .container.about {
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
    }

    .container-item {

    }

    .handpan-img {
        width: 100%;
        min-height: 200px;
        background-image: url(/static/media/handpan-wide.e8d8ecfd.jpeg);
        background-size: cover;
    }

}

.App {
  text-align: center;
  background-color: #ebebeb63;
  overflow: scroll;
  /* background-image: url(./pictures/bg.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat; */
  width: 100vw;
  height: 100vh;
  white-space: pre-line;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.header {
    background-image: url(/static/media/header2.6b780c81.jpg);
    background-position: -40px 47%;
    background-size: cover;
    background-repeat: no-repeat;
    border-bottom: 1px groove;
    background-color: #ebebeb;
  }

  @media screen and (max-width: 768px) {
    .header{
      background-position: -23vw 47%;
    }
  }

.menuBar{
    margin-bottom: 2vw;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    margin-top: 2vw;
}

.menuItems{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}

.link{
    text-decoration: none;
    color: black;
    font-size: larger;
    font-style: oblique;
}
.offerHeader{
    font-family: ByHand;
    font-size: xxx-large;
}

.price{
    font-size: calc(0.5em + 0.7vw);
    margin: 1vw;
}

.offer-img{
    object-fit: cover;
}

.offerDesc{
    /* font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif; */
    font-size: calc(0.5rem + 1vw);
}

.book-btn{
    background-color: #ebebeb63;
    padding: 15px 32px 15px 32px;
    font-size: calc(0.5em + 1vw);
    cursor: pointer;
    width: 30%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    height: 8%;
    margin-top: 0.8rem;
}

@media screen and (max-width: 768px) {
    .offer-img{
        width: 100% !important;
        height: 40vw;
    }

    .offerHeader{
        margin-bottom: 0.8rem;
        font-size: calc(1rem + 1vw);
    }

    .book-btn{
        padding: 15px 18px 15px 18px;
        height: 8vw;
    }

    .price{
        margin: 7vw 0vw 7vw 0vw;
    }
}

.container{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    margin: 5vw;
    height: 100%;
}

.container:not(:last-child){
    margin-bottom: 15vw
}

.reversed{
    -webkit-flex-direction: row-reverse;
            flex-direction: row-reverse;
}

.container-item{
    width: 50%;
    margin: 5vw;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-items: center;
            align-items: center;
}


.half-screen{
    width: 50%;
}

@media screen and (max-width: 768px) {
    .container-item{
        font-size: calc(0.5em + 1vw);
        width: inherit;
    }

    .container{
        -webkit-flex-direction: column-reverse;
                flex-direction: column-reverse;
        height: auto;
    }

    .reversed{
        -webkit-flex-direction: column-reverse;
                flex-direction: column-reverse;
    }
 }

.footer {
    border-top: 1px groove;
    font-size: 0.5rem;
  }
.ayatana-container{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.ayatana-header{
    padding-top: 2vh;
    font-size: calc(2rem + 1vw);
}

.ayatana-desc{
  margin: 5vw;
  width: 80%;
  font-size: calc(0.35rem + 1vw);
}

.ayatana-list{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.ayatana-img{
  width: 100%;
  height: 45vh;
  background-position: 0 66%;
  background-image: url(/static/media/buddah.5d645d2b.jpg);
  background-size: cover;
}

@media screen and (max-width: 768px) {
  .ayatana-img{
    height: 22vh;
  }


}
.contact-container{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.image{
    width: 100%;
    height: 45vh;
    object-fit: cover;
    object-position: 0 58%;
}

@media screen and (max-width: 768px) {
    .image{
        height: 27vh;
    }
}
.email-container{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 50%;
}
.contact-form{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    margin: 0vw;
    padding: 5vw ;
    position:relative;
}

.contact-form input, textarea{
    display: block;
    width: 80%;
    padding: .5rem .8rem .5rem .8rem;
    margin: .9vw 0 ;
    border:0;
    background-color: #f7f7f7;
    border-bottom: 1px solid black;
}

textarea{
    resize: none;
    height: 15vh;
}

textarea:focus, input:focus{
    outline: none;
}

.send-btn{
    color: white;
    background: black;
    width: 60%;
    margin-top: 1vw;
}

@media screen and (max-width: 768px){
    .email-container{
        width: 100%;
    }
}
